import { Link } from 'gatsby'
import PropTypes from 'prop-types'
import React from 'react'

// styled
import {
  HeaderWrapper,
  Navigation,
  NavigationUl,
  NavigationLi,
  NavigationLink,
  SiteTitleWrapper,
  LogoWrapper,
  Logo,
  SiteTitle,
  Description,
} from '../styling/header.style'

class Header extends React.Component {
  state = {
    colorMenu: `rgb(39,39,39)`,
    bgLogo: `rgb(22,160,133)`,
  }

  componentDidMount() {
    this.changeColorMenu()
  }

  changeColorMenu = () => {
    document.addEventListener('scroll', () => {
      this.setState((prevState, props) => {
        if (window.scrollY > 118)
          return {
            colorMenu: `rgb(255, 255, 255)`,
          }
        else
          return {
            colorMenu: `rgb(39,39,39)`,
          }
      })
    })
  }

  render() {
    const { siteTitle, siteDescription } = this.props
    return (
      <div>
        <HeaderWrapper>
          <Navigation>
            <NavigationUl>
              <NavigationLi>
                <NavigationLink
                  colorMenu={this.state.colorMenu}
                  colorHover={`rgb(22, 160, 133)`}
                  to={`/`}
                >
                  Home
                </NavigationLink>
              </NavigationLi>
              <NavigationLi>
                <NavigationLink
                  colorMenu={this.state.colorMenu}
                  colorHover={`rgb(231, 76, 60)`}
                  to={`/about`}
                >
                  About
                </NavigationLink>
              </NavigationLi>
              <NavigationLi>
                <NavigationLink
                  colorMenu={this.state.colorMenu}
                  colorHover={`rgb(41, 128, 185)`}
                  to={`/project`}
                >
                  Project
                </NavigationLink>
              </NavigationLi>
              <NavigationLi>
                <NavigationLink
                  colorMenu={this.state.colorMenu}
                  colorHover={`#ffbd4c`}
                  to={`/contact`}
                >
                  Contact
                </NavigationLink>
              </NavigationLi>
            </NavigationUl>
          </Navigation>
          <SiteTitleWrapper>
            <LogoWrapper>
              <Logo bgLogo={this.state.bgLogo}>SK</Logo>
              <SiteTitle>{siteTitle}</SiteTitle>
            </LogoWrapper>
            <Description>{siteDescription}</Description>
          </SiteTitleWrapper>
        </HeaderWrapper>
      </div>
    )
  }
}

Header.propTypes = {
  siteTitle: PropTypes.string,
  siteDescription: PropTypes.string,
}

Header.defaultProps = {
  siteTitle: '',
  siteDescription: '',
}

export default Header
