import React from 'react'
import { Link, graphql } from 'gatsby'

import Layout from '../components/layout'

import {
  Window,
  TitleBar,
  Button,
  ButtonRed,
  ButtonYellow,
  ButtonGreen,
  Content,
  Title,
} from '../styling/page.style'

const Project = ({ data }) => {
  return data.allPagesJson.edges.map(({ node, index }) => {
    return (
      <Layout>
        <h2>{node.title}</h2>
        <p>{node.summary}</p>
        <Window>
          <TitleBar>
            <Button>
              <ButtonRed />
              <ButtonYellow />
              <ButtonGreen />
            </Button>
          </TitleBar>
          <Content>
            <div key={index}>
              {node.projects.map((item, index) => {
                return (
                  <div>
                    <Title style={{ fontSize: '20px' }}>
                      /* {item.project_title} */
                    </Title>
                    <div>Built for: {item.description}</div>
                    <div>Built with: {item.tools}</div>
                  </div>
                )
              })}
            </div>
          </Content>
        </Window>
      </Layout>
    )
  })
}

export default Project

export const query = graphql`
  query {
    allPagesJson(filter: { path: { eq: "project" } }) {
      edges {
        node {
          path
          title
          summary
          projects {
            project_title
            link_project
            description
            tools
          }
        }
      }
    }
  }
`
